import { Injectable } from '@angular/core';
import {
  Firestore,
  collection,
  collectionData,
  doc,
  docData,
  addDoc,
  deleteDoc,
  updateDoc,
  serverTimestamp,
  query,
  where,
  DocumentData,
  arrayRemove,
  arrayUnion,
  orderBy
} from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { FirebaseCollections } from 'src/app/constants/fb-collections';
import { Course, Test, Order } from 'src/app/models/course.interface';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(
    private firestore: Firestore
  ) { }


  /**
   * @description Firebase Data
   */
  get(collectionName: string): Observable<any[]> {
    const ref = collection(this.firestore, collectionName);
    const queryRef = query(ref, orderBy('created_at', 'asc'));
    return collectionData(queryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getById(collectionName: string, id: string): Observable<any> {
    const docRef = doc(this.firestore, collectionName + `/${id}`);
    return docData(docRef, { idField: 'id' }) as Observable<any>;
  }

  getByUID(collectionName: string, uid: string) {
    console.log(collectionName, uid);
    const ref = collection(this.firestore, collectionName);
    const queryRef = query(ref, where('uid', '==', uid), orderBy('created_at', 'asc'));
    return collectionData(queryRef, { idField: 'id' }) as Observable<any[]>;
  }

  add(collectionName: string, data: any) {
    const ref = collection(this.firestore, collectionName);
    return addDoc(ref, { ...data, created_at: serverTimestamp() });
  }

  delete(collectionName: string, data: any) {
    const docRef = doc(this.firestore, collectionName + `/${data.id}`);
    return collectionName === FirebaseCollections.MEDIA || collectionName === FirebaseCollections.MEDIA ?
      updateDoc(docRef, { media: arrayRemove(data?.media), updated_at: serverTimestamp() }) :
      deleteDoc(docRef);
  }

  update(collectionName: string, data: any) {
    const docRef = doc(this.firestore, collectionName + `/${data.id}`);
    return collectionName === FirebaseCollections.MEDIA || collectionName === FirebaseCollections.MEDIA ?
      updateDoc(docRef, { media: arrayUnion(...data?.media), updated_at: serverTimestamp() }) :
      updateDoc(docRef, { ...data, updated_at: serverTimestamp() });
  }

  /**
   * @description Course Data
   */
  getCourses(): Observable<Course[]> {
    const coursesRef = collection(this.firestore, 'courses');
    return collectionData(coursesRef, { idField: 'id' }) as Observable<Course[]>;
  }

  getCourseById(id): Observable<Course> {
    const courseDocRef = doc(this.firestore, `courses/${id}`);
    return docData(courseDocRef, { idField: 'id' }) as Observable<Course>;
  }

  addCourse(course: Course) {
    const coursesRef = collection(this.firestore, 'courses');
    return addDoc(coursesRef, { ...course, created_at: serverTimestamp() });
  }

  deleteCourse(courseId: string) {
    const courseDocRef = doc(this.firestore, `courses/${courseId}`);
    return deleteDoc(courseDocRef);
  }

  updateCourse(courseId: string, course: any) {
    const courseDocRef = doc(this.firestore, `courses/${courseId}`);
    return updateDoc(courseDocRef, { ...course, updated_at: serverTimestamp() });
  }

  /**
   * @description Test Data
   */
  getTests(courseId: string): Observable<Test[]> {
    const testsRef = collection(this.firestore, 'tests',);
    const testQueryRef = query(testsRef, where('course_id', '==', courseId || ''));
    return collectionData(testQueryRef, { idField: 'id' }) as Observable<Test[]>;
  }

  getAnswerSheets(testId: any): Observable<any[]> {
    const answerSheetRef = collection(this.firestore, FirebaseCollections.ANSWER_SHEET);
    const scheduleQueryRef = query(answerSheetRef, where('test_id', '==', testId || ''));
    return collectionData(scheduleQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getValuedAnswerSheet(testId: any): Observable<any[]> {
    const answerSheetRef = collection(this.firestore, FirebaseCollections.VALUED_ANSWER_SHEET);
    const scheduleQueryRef = query(answerSheetRef, where('test_id', '==', testId || ''));
    return collectionData(scheduleQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getRemarksByTestId(testId: string, studentId: string): Observable<any[]> {
    const scheduleRef = collection(this.firestore, FirebaseCollections.STUDENT_ACTIVITY);
    const scheduleQueryRef = query(scheduleRef, where('test_id', '==', testId || ''), where('student_id', '==', studentId || ''));
    return collectionData(scheduleQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getScheduleByCourseId(courseId: string): Observable<any[]> {
    const scheduleRef = collection(this.firestore, FirebaseCollections.SCHEDULES);
    const scheduleQueryRef = query(scheduleRef, where('course_id', '==', courseId));
    return collectionData(scheduleQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getScheduleByTestId(testId: string): Observable<any[]> {
    const scheduleRef = collection(this.firestore, FirebaseCollections.SCHEDULES);
    const scheduleQueryRef = query(scheduleRef, where('test_id', '==', testId || ''));
    return collectionData(scheduleQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getQuestionPaperByTestId(testId: string): Observable<any[]> {
    const questionPapersRef = collection(this.firestore, FirebaseCollections.QUESTION_PAPERS);
    const questionPapersQueryRef = query(questionPapersRef, where('test_id', '==', testId || ''));
    return collectionData(questionPapersQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getTestById(id): Observable<Test> {
    const testDocRef = doc(this.firestore, `tests/${id}`);
    return docData(testDocRef, { idField: 'id' }) as Observable<Test>;
  }

  addTest(test: Test): DocumentData {
    const testsRef = collection(this.firestore, 'tests');
    return addDoc(testsRef, { ...test, created_at: serverTimestamp() });
  }

  deleteTest(test: Test) {
    const testDocRef = doc(this.firestore, `tests/${test.id}`);
    return deleteDoc(testDocRef);
  }

  updateTest(testId: string, test: any) {
    const testDocRef = doc(this.firestore, `tests/${testId}`);
    return updateDoc(testDocRef, { ...test, updated_at: serverTimestamp() });
  }

  /**
   * @description Order Data
   */
  getOrders(): Observable<Order[]> {
    const orderDocRef = collection(this.firestore, 'orders');
    return collectionData(orderDocRef, { idField: 'id' }) as Observable<Order[]>;
  }

  getActiveOrdersWithStudentId(studentId: string): Observable<any[]> {
    const scheduleRef = collection(this.firestore, FirebaseCollections.ORDERS);
    const scheduleQueryRef = query(scheduleRef, where('student_id', '==', studentId || ''), where('status', '==', true));
    return collectionData(scheduleQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  getOrderById(id): Observable<Course> {
    const orderDocRef = doc(this.firestore, `orders/${id}`);
    return docData(orderDocRef, { idField: 'id' }) as Observable<Course>;
  }

  addOrder(order: Order) {
    const orderDocRef = collection(this.firestore, 'orders');
    return addDoc(orderDocRef, { ...order, created_at: serverTimestamp() });
  }

  deleteOrder(order: Order) {
    const orderDocRef = doc(this.firestore, `orders/${order.id}`);
    return deleteDoc(orderDocRef);
  }

  updateOrder(course: any) {
    const orderDocRef = doc(this.firestore, `orders/${course.id}`);
    return updateDoc(orderDocRef, { ...course, updated_at: serverTimestamp() });
  }

  /**
   * @description Order Data
   */
  getRegisteredStudents(): Observable<any[]> {
    const studentRef = collection(this.firestore, FirebaseCollections.STUDENTS);
    return collectionData(studentRef, { idField: 'id' }) as Observable<any[]>;
  }

  getSubscribedStudents(): Observable<any[]> {
    const studentRef = collection(this.firestore, FirebaseCollections.STUDENTS);
    return collectionData(studentRef, { idField: 'id' }) as Observable<any[]>;
    // const questionPapersRef = collection(this.firestore, FirebaseCollections.STUDENTS);
    // const questionPapersQueryRef = query(questionPapersRef, where('test_id', '==', testId || ''));
    // return collectionData(questionPapersQueryRef, { idField: 'id' }) as Observable<any[]>;
  }

  /**
   * @description Notifications Data
   */
  getNotifications(): Observable<Course[]> {
    const coursesRef = collection(this.firestore, FirebaseCollections.NOTIFICATIONS);
    return collectionData(coursesRef, { idField: 'id' }) as Observable<Course[]>;
  }

  getUnreadNotifications(): Observable<Course[]> {
    const testsRef = collection(this.firestore, FirebaseCollections.NOTIFICATIONS);
    const testQueryRef = query(testsRef, where('status', '==', 'unread'));
    return collectionData(testQueryRef, { idField: 'id' }) as Observable<Test[]>;
  }

  /**
   * @description Chat Data
   */
  getChats(): Observable<Course[]> {
    const chatRef = collection(this.firestore, FirebaseCollections.CHAT_ROOM);
    const chatQueryRef = query(chatRef, orderBy('updated_at', 'desc'));
    return collectionData(chatQueryRef, { idField: 'id' }) as Observable<Test[]>;
  }

  getMessages(chatId: string): Observable<Course[]> {
    const messageRef = collection(this.firestore, FirebaseCollections.MESSAGES);
    const messageQueryRef = query(messageRef, where('chat_id', '==', chatId), orderBy('created_at', 'asc'));
    return collectionData(messageQueryRef, { idField: 'id' }) as Observable<Test[]>;
  }
}
