import { Component, OnInit } from '@angular/core';
import { FirebaseCollections } from 'src/app/constants/fb-collections';
import { CommonService } from 'src/app/services/common/common.service';
import { DataService } from 'src/app/services/data/data.service';

@Component({
  selector: 'app-chat-room',
  templateUrl: './chat-room.page.html',
  styleUrls: ['./chat-room.page.scss'],
})
export class ChatRoomPage implements OnInit {

  public chatRooms: Array<any> = [];
  private chatRoomSubscriber: any = null;

  constructor(
    private dataService: DataService,
    private commonService: CommonService
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.chatRoomSubscriber = this.dataService.getChats().subscribe(resp => {
      console.log(resp);
      this.chatRooms = resp;
    }, err => {
      console.error('Error while getting chats::::::::::::\n', err);
    });
  }

  ionViewWillLeave() {
    this.chatRoomSubscriber.unsubscribe();
  }

  openChat(chat: any) {
    localStorage.setItem('chat', JSON.stringify(chat));
    this.commonService.navigateForward('chat');
  }
}
